<template>
    <div class="blog" id="blog">
        <el-container>
            <el-header>
                <el-row class="blog-header">
                    <el-col :span="2" :push="2" class="blog-logo">恒</el-col>
                    <el-col  :span="18" :push="2"  class="blog-nav">
                        <el-row>
                            <el-col :span="12" class="nav-left">
                                <ul>
                                    <li @click="linkTo('/blog/list')">Articles</li>
                                    <li>Discussions</li>
                                </ul>
                            </el-col>
                            <el-col :span="12" :push="3" class="nav-right">
                                <ul>
                                    <li><el-input v-model="searchForm.keyWord" placeholder="请输入内容"></el-input></li>
                                    <li @click="linkTo('/login')">Login In</li>
                                    <li>Register</li>
                                </ul>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </el-header>
            <el-main>
                <transition  name="custom-classes-transition"
                             enter-active-class="animated lightSpeedIn"
                             leave-active-class="animated lightSpeedOut">
                <!--list-->
                <list-page class="blogLayout"  v-if="blogPage==='list'|| blogPage===''"></list-page>
                <!--list-->
                <!--login-->
                <!--<login-page v-else-if="blogPage==='login'"></login-page>-->
                <!--login-->
                <!--detail-->
                <detail-page class="blogLayout" v-else-if="blogPage==='detail'"></detail-page>
                <!--detail-->
                </transition>
                <div class="blog-main-footer">
                    <div class="link">
                        <i class="iconfont icon-home" @click="scrollTop"></i>
                        <i class="iconfont icon-github-fill"></i>
                        <i class="iconfont icon-twitter-circle-fill"></i>
                    </div>
                </div>
            </el-main>
            <el-footer>
                <div class="blog-footer">
                    <el-row>
                        <el-col>
                            <div class="identity">
                                © HWJY Blog 2018. Powered By WeiHeng Ling
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-footer>
        </el-container>
    </div>
</template>

<script>
import listPage from "../../components/blog/list.vue";
import loginPage from "../../components/blog/login.vue";
import detailPage from "../../components/blog/detail.vue";
export default {
  name: "list",
  components: { listPage, loginPage, detailPage },
  data() {
    return {
      searchForm: {
        keyWord: ""
      },
      blogPage: "list"
    };
  },
  watch: {
    $route: {
      handler(to, from) {
        console.log(to, from);
        console.log(to.path.split("/"));
        this.blogPage = to.path.split("/")[2];
        console.log(this.blogPage);
      },
      immediate: true
    }
  },
  methods: {
    linkTo(link) {
      this.$router.push(link);
    },
    scrollTop() {
      let back = setInterval(() => {
        if (document.body.scrollTop || document.documentElement.scrollTop) {
          document.body.scrollTop -= 10;
          document.documentElement.scrollTop -= 10;
        } else {
          clearInterval(back);
        }
      });
    }
  }
};
</script>

